.header {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: transparent;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.0);
  transition: all ease 0.2s;
  transition-property: box-shadow, background;

  &.live {
    top: auto;
    bottom: 0;
    background: rgba(32, 42, 67, 0.9);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);

    @include md {
      background: transparent;
      box-shadow: none; } }

  &.is-open {
    top: 0;
    bottom: 0;

    .header__inner {
      top: 0;
      opacity: 1;
      visibility: visible;
      z-index: 1;
      padding-left: 0;
      pointer-events: auto; } }


  &.is-sticky {
    background: rgba(32, 42, 67, 0.9);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); }

  @include md {
    bottom: auto;
    top: 0;
    padding: 20px 20px;
    display: flex;
    justify-content: flex-end;
    background-color: transparent;
    pointer-events: none;

    &.is-sticky {
      background: transparent;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.0); } }

  &__nav {
    @include md {
      width: 100%;
      margin-bottom: 10px; } }
  &__btn {
    position: relative;
    margin: 0 0 0 30px;

    .btn {
      width: 100%;
      height: 54px;
      padding: 0 28px;
      border-width: 2px; }

    @include tablet {
      width: 160px; }
    @include md {
      width: auto;
      position: relative;
      margin: 0 0 20px; } }

  &__alt-btn {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 2;
    margin: 0;
    width: auto;
    pointer-events: auto;

    .btn {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      border-width: 2px; }

    @include md {
      display: block;
      display: none; } }

  &__socials {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0 auto 0 0;

    @include md {
      margin: 10px auto 0;
      order: 3;
      position: relative;
      left: 0;
      top: 0;

      .icon {
        height: 20px; } } }

  &__inner {
    position: relative;
    display: flex;
    box-sizing: content-box;
    max-width: 1480px;
    padding: 17px 5vw;
    align-items: center;
    justify-content: center;
    min-height: 50px;
    margin: 0 auto;

    @include md {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      height: 100vh;
      height: calc(var(--vh, 1vh) * 100);
      padding: 10px 0;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      background-color: rgba(32, 42, 67, 0.5);
      z-index: -10; } }

  &__burger {
    display: none;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 2;
    @include md {
      display: inline-block;
      pointer-events: auto; } } }

.nav {
  @include medium;
  display: flex;
  @include md {
    display: block; }
  &__item {}

  &__link {
    text-decoration: none;
    text-transform: uppercase;
    color: $white;
    transition: 0.3s;
    font-size: 14px;
    height: 54px;
    display: flex;
    padding: 0 8px;
    align-items: center;
    justify-content: center;

    &:hover {
      color: #9dde00; }

    @include md {
      font-size: 14px;
      display: flex;
      height: auto;
      text-align: center;
      padding: 20px 20px;
      font-weight: 500; }
    @include sm {
      font-size: 12px; }
    .no-touch &:hover {
      color: $themeColor; } } }

.burger {
  width: 60px;
  height: 60px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include sm {
    width: 50px;
    height: 50px; }

  span {
    transition: 0.3s;
    display: block;
    height: 4px;
    width: 36px;
    background-color: #ffffff;
    border-radius: 5px;
    &:not(:last-child) {
      margin-bottom: 6px; } }
  &.is-active {
    span {
      &:nth-child(1) {
        transform: translate(0, 10px) rotate(45deg); }
      &:nth-child(2) {
        transform: translateX(0);
        opacity: 0; }
      &:nth-child(3) {
        transform: translate(0, -10px) rotate(-45deg); } } } }
