.btn {
  box-sizing: border-box;
  display: flex;
  cursor: pointer;
  text-align: center;
  background-color: $themeColor;
  background: transparent;
  padding: 16px 20px;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 14px;
  @include bold;
  color: $white;
  text-decoration: none;
  transition: color, border-color, background ease 0.3s;
  transform: translateZ(0) perspective(1px);
  backface-visibility: hidden;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-backface-visibility: hidden;
  border: 3px solid $themeColor;
  border-radius: 26px;

  &:hover {
    background-color: $themeColor; }

  @include tablet {
    padding: 15px 20px; }

  &--white {
    border-color: $white;

    &:hover {
      color: $mainBg;
      background-color: $white; } }

  &--gradient {
    border: 2px solid transparent;
    color: #000;
    font-size: 14px;

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: -2;
      background: linear-gradient(145deg, $themeColor, $themeColorD);
      animation: AnimationName 5s linear infinite; }

    &:before {
      transition: 0.2s;
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
      background: linear-gradient(145deg, $themeColorD, $themeColor);
      animation: AnimationName 5s linear infinite; }

    &:hover {
      &:before {
        opacity: 0; } }
    @include tablet {
      padding: 19px 23px; } }

  &--green {
    color: $themeColor;

    &:hover {
      color: $white; } }

  &--transparent {
    border: 2px solid transparent;
    background-color: transparent;
    color: $themeColor;

    &:hover {
      background: transparent;
      border: 3px solid $themeColor; } }

  &--border {
    border: 3px solid $themeColor;
    color: $black;
    font-size: 14px;
    background: transparent;

    &:hover {
      color: #000;
      &:before {
        opacity: 0; } }

    @include tablet {
      padding: 15px 23px; } } }

.icon-link {
  color: #fff;
  .icon {
    font-size: 18px;
    fill: $white; } }

.theme-link {
  font-size: 14px;
  color: $themeColor;
  text-transform: uppercase;
  text-decoration: none;
  &:hover {
    text-decoration: underline; } }
